import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        isAuth: localStorage.getItem('auth') ? localStorage.getItem('auth') : ''
    },
    mutations: {
        auth_user(state, token) {
            localStorage.setItem('auth', token);
            state.isAuth = token;
        },
        remove_auth(state) {
            localStorage.removeItem('auth');
            state.isAuth = '';
        },
        refresh_auth(state, token) {
            localStorage.setItem('auth', token);
            state.isAuth = token;
        }
    },
    actions: {
        login({ commit }, token) {
            return new Promise((resolve, reject) => {
                commit('auth_user', token);
                resolve();
            });
        },
        logout({ commit }) {
            return new Promise((resolve, reject) => {
                commit('remove_auth');
                resolve();
            })
        },
        refreshToken({ commit }, token) {
            return new Promise((resolve, reject) => {
                commit('refresh_auth', token);
                resolve();
            })
        }
    },
    getters: {
        isAuth: state => state.isAuth,
    }
});