<template>
<div>
  <nav class="navbar navbar-expand app-navbar">
    <router-link to="/" class="app-home"> <b-icon icon="house"></b-icon>Home</router-link>
    <ul class="navbar-nav text-white ml-auto">
      <li class="nav-item ml-2" v-if="!token"><router-link to="/register" class="nav-item">Register</router-link></li>
      <li class="nav-item ml-2"><router-link to="/login" class="nav-item" v-if="!token">Login</router-link></li>

<!-- My account -->

<div v-if="token">
  <b-dropdown class="vf-my-account">
    <template #button-content>
      <b-icon icon="person"></b-icon> Account
    </template>
    <b-dropdown-item><router-link to="/profile"><b-icon icon="person-fill"></b-icon>Profile</router-link></b-dropdown-item>
    <hr>
    <b-dropdown-item @click="logout()"><b-icon icon="power" aria-hidden="true"></b-icon> Logout</b-dropdown-item>
  </b-dropdown>
</div>
</ul>
</nav>

<!-- Search bar -->
  <nav class="navbar app-navbar-search text-white">
    <div class="ml-auto">
  <b-input-group>
    <template #prepend>
      <b-input-group-text class="app-prepend-search">Search by movie</b-input-group-text>
    </template>
    <template #append>
      <b-button @click="searchMovieByQuery">
        <b-icon icon="search"></b-icon>
      </b-button>
    </template>
    <b-form-input class="vf-input-focus app-input-search" @keypress="searchMovieByQuery" v-model="movie.title"></b-form-input>
  </b-input-group>
</div>
</nav>
</div>
</template>

<script>
export default {
    name: 'Navbar',
      data(){
            return{
                movie: {title: ''},
          }
      },
    methods:{
      logout(){
        this.$store.dispatch('logout');
        if(this.$route.path !== '/') this.$router.push({path: '/'})
      },
        async searchMovieByQuery(event = false){
          let query = this.movie.title;
          if(event.type != 'click'){
          if(event.code == 'Enter'){ 
                if(query){
                  if(this.$route.path !== `/search/${query}`){ this.$router.push({path:`/search/${query}`});}
                  this.$root.$emit('searchMovieByQuery', query);
                }
          }
          }else{
                if(query){
                  if(this.$route.path !== `/search/${query}`){ this.$router.push({path:`/search/${query}`});}
                  this.$root.$emit('searchMovieByQuery', query);
              }
          }
          },
      },
      computed: {
        token : function() { return this.$store.getters.isAuth},
    }
}
</script>

<style scoped>

hr{
  width:80%
}

.app-navbar{
  position:relative;
  background-color:#17202A;
}

.app-navbar-search{
  background-color: #273746 !important;
}

.app-prepend-search{
  color: #D5D8DC
}

.app-input-search{
  width:600px;
}

.app-home{
  color:white;
}

.app-home:hover{
  color: #f73859;
}

.app-home svg{
  margin-right:0.5em;
}


</style>