import Vue from 'vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

import store from '../store'

Vue.use(VueRouter)
Vue.use(Vuex)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import ('../views/About.vue')
    },
    {
        path: '/register',
        name: 'Register',
        component: () =>
            import ('../views/Register.vue')
    },
    {
        path: '/login',
        name: 'Login',
        component: () =>
            import ('../views/Login.vue')
    },
    {
        path: '/profile',
        name: 'Profile',
        component: () =>
            import ('../views/Profile.vue'),
        meta: { requireAuth: true }
    },
    {
        path: '/search/:query?',
        name: 'Search',
        component: () =>
            import ('../views/Search.vue'),
    },
    {
        path: '/movie/:movie_id',
        name: 'Movie',
        component: () =>
            import ('../views/Movie.vue')
    },
    {
        path: '/review/movie/:movie_id',
        name: 'Review',
        component: () =>
            import ('../views/Review.vue'),
        meta: { requireAuth: true },
    },
    {
        path: '/review/update/:review_id',
        name: 'Review-update',
        component: () =>
            import ('../views/Review-update.vue'),
        meta: { requireAuth: true },
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
});

router.beforeEach((to, from, next) => {
    const profile = to.matched.some(record => record.meta.requireAuth);
    if (profile && store.state.isAuth == '') {
        next({ name: 'Login' })
    }
    next();
});

export default router