<template>
<div class="carouselMovie">
    <b-row>
         <b-col lg="3" md="6" sm="12" v-for="movie in movies" :key="movie.id" class="mt-2">
        <div class="vf-container" @click="getMovie(movie.id)">
          <p class="app-card-movie-title">{{movie.title}}</p>
            <b-img thumbnail fluid :src="url_poster+'/'+movie.poster_path" :alt="movie.original_title" :id="movie.id" class="vf-movie-information"></b-img>
         </div>
        </b-col>
    </b-row>
</div>
</template>

<script>
const api_url = process.env.VUE_APP_API_URL;


export default {
    name: 'CarouselMovie',
    data(){
        return{
            movies: [],
            url_poster : process.env.VUE_APP_API_POSTER,
        }
    },
    created() {
        this.getMovies();
    },
    methods:{
        getMovies(){
            this.axios.get(`${api_url}/movie`).then((response)=> {
            this.movies = response.data.movies
        }).catch((error) => {
            console.log(error)
        });
        },

        getMovie(id){
            this.$router.push({path:`/movie/${id}`});
        }
    },
}
</script>

<style scoped>

.vf-container{
  position: relative;
  cursor: pointer;
}

.vf-container:hover:after{
    content:'';
    position:absolute;
    background-color: rgba(0, 0, 0, 0.171);
    top:0;
    bottom:0;
    right:0;
    left:0;
}


.app-card-movie-title{
    position:absolute;
    bottom:0;
    padding:15px;
    transform: translate(-50%, -50%);
    text-align: center;
    left: 50%;
    width:100%;
    background-color:#232931;
    color: rgba(255, 255, 255, 0.774);
    z-index:1;
    box-shadow: 0px 3px 1em 0.1em #333a44;
    font-weight: bold;
    letter-spacing: 1px;
}






</style>