<template>
  <div class="carouselReview">
        <b-card border-variant="light" :header="review.movie_title" class="text-center mt-3 p-2" v-for="review in reviews" :key="review._id">
            <b-card-text class="text-justify p-2 app-text-line-clamp app-background-text-gradient">
                <b> by {{review.username}}</b>
                <hr>
                {{review.content}}</b-card-text>
                <a class="app-a" @click="$bvModal.show(review._id)" v-b-tooltip.hover title="Click me to read review!">Read more...</a>
                <a @click="goMovie(review.movie_id)" class="app-a app-a-see-movie">See movie </a>
        </b-card>
        <b-modal hide-footer v-for="(review,index) in reviews" :key="index" :title="review.movie_title" :id="review._id">
            <p>{{review.content}} </p>
            <b-button class="mt-4 app-button" block @click="$bvModal.hide(review._id)">Thanks!</b-button>
        </b-modal>
  </div>
</template>

<script>
const api_url = process.env.VUE_APP_API_URL;
export default {
    name:'CarouselReview',
    data:function(){
        return{
            reviews: ''
        }
    },
    created:function(){
        this.init();
    },
    methods:{
        init(){
            this.getReviews();
        },
        getReviews(){
            this.axios.get(`${api_url}/review`).then((response)=>{
                this.reviews = response.data.reviews;
            }).catch((error)=>{
                console.log(error);
            })
        },
        goMovie(movie_id){
            this.$router.push({path:`/movie/${movie_id}`})
        },
    }
}
</script>

<style scoped>

.card-header{
    font-size:1.5em;
    letter-spacing: 0.02em;
}

.app-a{
    cursor:pointer;
    margin-left:1em;
}

.app-a-see-movie{
    color: #f73859;
}

.app-background-text-gradient{

   background: -webkit-linear-gradient(#333, #333, #333, #333, #eee);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.app-text-line-clamp{
    display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;  
  overflow: hidden;
}
</style>